import * as React from 'react';
import  {useEffect, useState} from 'react'
import {useParams } from 'react-router-dom'
import AppBar from '@mui/material/AppBar';
import Button from 'react-bootstrap/Button';


import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { FaFacebook, FaTwitter, FaInstagram } from "react-icons/fa";





export default function Album() {

  const theme = createTheme();

const [records, setRecords] = useState([]);
    const params = useParams()

    useEffect(() => {
        
      fetchRecords();
    }, []);


    //gets list of pets
    const fetchRecords = async () => {

     /*   const recordId= JSON.parse(localStorage.getItem('record'))._id;
       console.log(`recordID is` + recordId); */

        let result = await fetch ("http://localhost:5000/api/petrecords") //(`http://localhost:5000/api/petrecords/${recordId}`)
        result = await result.json()
        setRecords(result)
      /*  const {data} = await axios.get('http://localhost:5000/api/pets')
        setPets(data) */
    };
   // console.log(pets);

   const deleteRecord = async (id) => {
    console.log(id)
    if (window.confirm("Are you sure?")) {     
    }

    let result = await fetch(`http://localhost:5000/api/petrecords/${id}`, {
        method: "DELETE",

    });
    result = await result.json();
    if (result){
        alert("Record deleted");
       
    //navigate("/")
        fetchRecords()
    }
  };

    const searchHandler = async(event) => {
      let key = event.target.value;
      console.log({key: key})
      if (key){
  
      let result = await fetch(`http://localhost:5000/api/petrecords/search/${key}`);
      result = await result.json()
      if (result) {
          setRecords(result)
      }
  }else{
      fetchRecords();
  }}
   

  return (

    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar>
          
          <Typography variant="h6" color="inherit" noWrap>
           Pet Records
          </Typography>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        <Button variant='primary'
          sx={{ color: 'white', backgroundColor: 'blue', borderColor: '' }}
          className='mt-4 ms-4' href= '/mypets'>Back to Dashboard</Button><br/>

        <Container sx={{ py: 8 }} maxWidth="md">
          {/* End hero unit */}

          

          <input type="" className='search-record-box' placeholder='Search Record' 
    onChange={searchHandler}/><br/><br/>
          <Grid container spacing={4}>
            { records.length >0 ?records.map((record) => (
              <Grid item key={record} xs={10} sm={6} md={4}
              >
                <Card
                  sx={{ height: '100%', display: 'flex', flexDirection: 'column'}}
                  
                >
                  <CardMedia
                    component="img"
                    sx={{
                      // 16:9
                      pt: '10%',
                    }}
                    image="https://source.unsplash.com/random"
                    alt="random"
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h5" component="h2">
                  {record.petName} 
                    <span> <h6 className='text-muted'>{record._id}</h6> </span>
                       
                      
                    </Typography><br/>
                    <Typography>
                    <h4>Vet</h4>
                        {record.vet.map ((c, i) =>
                        <div>
                        <h6></h6>{c.name} 
                        <h6></h6> {c.contact}
                        </div>)} <br/>

                        <h4>Health Concerns</h4>
                        {record.healthConcerns.map ((c, i) =>
                        <div>
                        <h6>Allergies:</h6>{c.allergies}
                        <h6>Medication:</h6> {c.medication}
                        <h6>Existing Conditions:</h6> {c.exisitingConditions} 
                        <h6>History:</h6> {c.history}
                        </div>
                        )}
                      
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button className="ms-3 mb-4" size="small" href={`update/${record._id}`}>Update</Button>
                    <Button className="ms-3 mb-4" size="small" variant="danger"
                    onClick= {() => deleteRecord(record._id)}>Delete</Button>
                  </CardActions>
                </Card>
              </Grid>
            ))
            :<h1>No Record Found</h1>

          }
          </Grid>
        </Container>
      </main>
      {/* Footer */}
      <Box sx={{ bgcolor: 'background.paper', p: 2 }} component="footer">
        <Typography variant="h6" align="center" gutterBottom>
       
        </Typography>
        <Typography
          variant="subtitle1"
          align="center"
          color="text.secondary"
          component="p"
        >
          <section className='mb-4'>


          <a href="#!" className='m-4' ><FaFacebook/></a>
            <a href="#!" className='m-4'><FaTwitter/></a>
            <a href="#!" className='m-4'><FaInstagram/></a>
   

</section>
        </Typography>
        <Typography variant="body1" color="text.secondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://mui.com/">
        PetBook
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
      </Box>
      {/* End footer */}
    </ThemeProvider>
  );
}